/* global jQuery MktoForms2 */
/*
 * Wrap Marketo form fields to support two column layout
 */
(function ($, MktoForms2) {

	"use strict";

	MktoForms2.onFormRender(function () {
		setTimeout(function(){
			$(".mktoSimple button[type=submit]").text("Request Demo");
		}, 0);
	});
}(jQuery, MktoForms2));
